button{
    color: white;
    background-color: #00ccc2;
    font-weight: bold;
    border: none;
    border-radius: 10px;
    padding: 1em;
    transition: .2s;
}

button:hover{
    background-color: aqua;
    transform: scale(1.1);
}

Tr:hover{
    background-color: #00ccc2;
    color: white;
    font-weight: 500;
}
